<template>
  <div class="indexContainer">
    <div class="top">
      <div>
        <i :style="{backgroundImage: 'url(' + logoUrl + ')'}"></i>
        <span>{{ systemName || '智能安全生产管控平台系统后台' }}</span>
      </div>
      <div>
        <i @click="toinfo"></i>
        <!-- <i>
          <el-badge is-dot class="item1"></el-badge>
        </i>-->
        <i @click="toinformation">
          <el-badge :is-dot="hasNews" class="item2"></el-badge>
        </i>
        <i>
          <el-dropdown>
            <span class="el-dropdown-link"></span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <button class="btns" @click="changeName2 = true;">修改姓名</button>
                <el-dialog title="修改姓名" :visible.sync="changeName2" append-to-body width="30%">
                  <el-form :model="form3">
                    <el-form-item label="姓名:" :label-width="formLabelWidth" required>
                      <el-input v-model="form3.name" autocomplete="off" placeholder="admin"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="changeName2 = false">取 消</el-button>
                    <el-button @click="changeName2 = false;updateName2();">确 定</el-button>
                  </div>
                </el-dialog>
              </el-dropdown-item>
              <el-dropdown-item>
                <button class="btns" @click="changePassword = true">修改密码</button>
                <el-dialog title="修改密码" :visible.sync="changePassword" append-to-body width="30%">
                  <el-form :model="form4">
                    <el-form-item label="原密码:" :label-width="width" required>
                      <el-input v-model="form4.old" autocomplete="off" placeholder="请输入原密码"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码:" :label-width="width" required>
                      <el-input
                        v-model="form4.new"
                        autocomplete="off"
                        placeholder="请输入新密码，6-20位，数字、字母、字符组成"
                      ></el-input>
                    </el-form-item>
                    <el-form-item label="重复密码:" :label-width="width" required>
                      <el-input v-model="form4.again" autocomplete="off" placeholder="再次输入密码"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="changePassword = false">取 消</el-button>
                    <el-button type="primary" @click="changePassword = false;updatePassword()">确 定</el-button>
                  </div>
                </el-dialog>
              </el-dropdown-item>
              <el-dropdown-item>
                <button @click="quit" class="btns">退出登录</button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </i>
        <i></i>
        <i>{{username}}</i>
      </div>
    </div>
    <div class="centerBody">
      <div class="left">
        <div class="left-top">
          <el-row class="tac">
            <el-col :span="3">
              <el-menu
                default-active="2"
                class="el-menu-vertical-demo"
                background-color="#011C31"
                text-color="#fff"
                active-text-color="#FFFFFF"
                router
              >
                <el-menu-item index="/Index/company">
                  <i class="el-icon-menu"></i>
                  <span slot="title">企业管理</span>
                </el-menu-item>
                <el-menu-item index="/Index/deploy">
                  <i class="el-icon-lollipop"></i>
                  <span slot="title">独立部署授权管理</span>
                </el-menu-item>
                <el-submenu index="3">
                  <template slot="title">
                    <i class="el-icon-setting"></i>
                    <span>系统设置</span>
                  </template>
                  <el-menu-item-group>
                    <el-menu-item index="/Index/message">官方消息管理</el-menu-item>
                    <el-menu-item index="/Index/dictionary">字典配置</el-menu-item>
                    <el-menu-item index="/Index/update">app更新设置</el-menu-item>
                    <el-menu-item index="/Index/log">操作日志</el-menu-item>
                    <el-menu-item index="/Index/control">远程控制</el-menu-item>
                    <el-menu-item index="/Index/pageSet">页面设置</el-menu-item>
                    <el-menu-item index="/Index/otherSet">其它设置</el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
              </el-menu>
            </el-col>
          </el-row>
        </div>
        <div class="left-bottom">
          <p>当前版本 V1.0</p>
          <p>技术支持：成都四象联创科技有限公司</p>
        </div>
      </div>
      <div class="right">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("center");
export default {
  data() {
    return {
      changeName2: false,
      form3: {
        name: ""
      },
      formLabelWidth: "60px",
      changePassword: false,
      form4: {
        old: "",
        new: "",
        again: ""
      },
      width: "100px",
      logoUrl: "",
      systemName: "",
      hasNews: false
    };
  },
  computed: {
    ...mapState(["backgroundUrl"]),
    username() {
      return sessionStorage.getItem("name");
    }
  },
  mounted() {
    // location.reload()
    this.getLogo();
    this.getNews();
  },
  methods: {
    ...mapActions([
      "changeNameAsync",
      "changePasswordAsync",
      "logoMeta",
      "newsStatusAsync"
    ]),
    quit() {
      this.$router.push(`/Login`);
    },
    toinfo() {
      this.$router.push(`/Info`);
    },
    toinformation() {
      this.$router.push(`/Index/informationCenter`);
      this.hasNews=false;
    },
    async getLogo() {
      await this.logoMeta();
      this.systemName = this.backgroundUrl.data.systemName;
      this.logoUrl = this.$download + this.backgroundUrl.data.logoUrl;
    },
    //消息中心状态
    async getNews() {
      const data = await this.newsStatusAsync();      
      if (data.length >0) {
        this.hasNews = true;
      } else if (data.length == 0) {
        this.hasNews = false;
      }
    },
    //修改名字
    async updateName2() {
      let newName = this.form3.name;
      const data = await this.changeNameAsync({ userName: newName });
      if (data.code === 200) {
        this.form3.name = "";
        this.$message("修改成功");
        this.$router.push(`/Login`);
      } else {
        this.$message("修改失败");
      }
    },
    //修改密码
    async updatePassword() {
      let oldword = this.form4.old;
      let newword = this.form4.new;
      let againword = this.form4.again;
      const data = await this.changePasswordAsync({
        oldPwd: oldword,
        newPwd: newword,
        verPwd: againword
      });
      if (data.data.code === 200) {
        this.form4.old = "";
        this.form4.new = "";
        this.form4.again = "";
        this.$message("修改成功");
        this.$router.push(`/Login`);
      } else if (data.data.code === 201) {
        this.form4.old = "";
        this.form4.new = "";
        this.form4.again = "";
        this.$message("三条数据不完整，请全部输入");
      } else if (data.data.code === 202) {
        this.form4.old = "";
        this.form4.new = "";
        this.form4.again = "";
        this.$message("初始密码错误，请重新输入");
      } else if (data.data.code === 203) {
        this.form4.old = "";
        this.form4.new = "";
        this.form4.again = "";
        this.$message("两次输入的密码不一致，请重新输入");
      } else if (data.data.code === 204) {
        this.form4.old = "";
        this.form4.new = "";
        this.form4.again = "";
        this.$message("密码格式错误，请重新输入");
      }
    }
  }
};
</script>

